@font-face {
    font-family: "Vary";
    src: url("../../fonts/monotype-vary-light.otf") format("woff2");
  }
  
  .product-slides2 {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 2.5rem 0 2.5rem 0;
  }
  .product-slides2 .main-slide2 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .product-slides2 .left-slides {
      width: 50%;
      z-index: 9999;
      display: flex;
      justify-content: center;
      align-items: center;
  }
  
  .product-slides2 .left-slides img {
    width: auto;
    height: 450px;
  }
  .product-slides2 .right-slides h4 {
      font-size: 1.6rem;
      font-family: 'Vary',sans-serif!;
      font-weight: 700;
  }
  .product-slides2 .right-slides p {
      font-size: 1.2rem;
      font-family: sans-serif!;

  }
  .product-slides2 .right-slides {
    padding-right: 7rem;
    z-index: 9999;
    width: 50%;

  }
